.postCard {
	display: flex;
	flex-direction: column;
	background-color: $primary-color;
	margin: 3rem 0.4rem;
	padding: 0.6rem;
	border-radius: 4px;
	border: 1px solid lighten($secondary-color, 60%);
}

.postCard__userInfo {
	display: flex;
	align-items: center;
	img {
		margin-left: 10px;
		width: 40px;
		border-radius: 100%;
	}
	p {
		padding-left: 35px;
		font-size: 1rem;
		font-family: $primary-font;
		font-weight: 600;
		background-clip: text;
		background: $gradient-color;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		cursor: pointer;
	}
}

.postCard__image {
	position: relative;
	width: 100%;
	img {
		width: 100%;
		margin-top: 10px;
	}
}

.postCard__meta--icons {
	svg {
		font-size: 28px;
		margin-top: 5px;
		margin-right: 10px;
		color: $secondary-color;
		animation: toggleAnimation 0.3s ease;
	}
}

@keyframes toggleAnimation {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.2);
	}
	100% {
		transform: scale(1);
	}
}

.postCard__likes {
	margin-top: 4px;
	color: $link-color;
	font-family: $primary-font;
	cursor: pointer;
	background: $gradient-color;
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.postCard__caption {
	display: flex;
	align-items: center;
	margin-top: 3px;
}

.postCard__caption--username {
	color: $link-color;
	font-weight: 600;
	background-clip: text;
	background: $gradient-color;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	cursor: pointer;
}

.postCard__caption--body {
	margin-left: 5px;
}

.postCard__comments {
	text-decoration: none;
	p {
		color: $link-color;
		margin-top: 6px;
		background-clip: text;
		text-decoration: none;
		background: $gradient-color;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		cursor: pointer;
	}
}

.postCard__date {
	font-size: 0.9rem;
	margin-top: 10px;
	color: lighten($secondary-color, 30%);
}
